define('skylark-langx/_attach',[],function(){
    return  function attach(obj1,path,obj2) {
        if (typeof path == "string") {
            path = path.split(".");//[path]
        };
        var length = path.length,
            ns=obj1,
            i=0,
            name = path[i++];

        while (i < length) {
            ns = ns[name] = ns[name] || {};
            name = path[i++];
        }

        return ns[name] = obj2;
    }
});
define('skylark-langx/skylark',[
    "./_attach"
], function(_attach) {
    var skylark = {
    	attach : function(path,obj) {
    		return _attach(skylark,path,obj);
    	}
    };
    return skylark;
});

define('skylark-ui-inputs/inputs',[
	"skylark-langx/skylark"
],function(skylark) {
	var inputs = {};

	return skylark.attach("ui.inputs",inputs);

});


define('skylark-ui-inputs/main',[
	"./inputs"
],function(){
	return inputs;
});
define('skylark-ui-inputs', ['skylark-ui-inputs/main'], function (main) { return main; });

